/* switch开关 */
.invoice-switch-wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  height: 0.5rem;
  line-height: 0.5rem;
  position: relative;
}
.invoice-switch-wrapper p {
  margin: 0;
  padding-left: 0.1rem;
  font-size: 0.13rem;
}
.invoice-switch-wrapper:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: -0.015rem;
  right: 0;
  height: 2px;
  border-top: 1px solid #D9D9D9;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.invoiceBtn {
  padding: 0.09rem 0.1rem 0 0;
}
.invoiceBtn .switch {
  display: none;
}
.invoiceBtn label {
  position: relative;
  display: block;
  padding: 0.005rem;
  border-radius: 0.12rem;
  height: 0.22rem;
  margin-bottom: 0.075rem;
  background-color: #eee;
  cursor: pointer;
  vertical-align: top;
  -webkit-user-select: none;
  width: 0.45rem;
}
.invoiceBtn label:before {
  content: '';
  display: block;
  border-radius: 0.12rem;
  height: 0.22rem;
  background-color: #f3f3f3;
  -webkit-transform: scale(1, 1);
  -webkit-transition: all 0.3s ease;
}
.invoiceBtn label:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -0.11rem;
  margin-left: -0.11rem;
  width: 0.22rem;
  height: 0.22rem;
  border-radius: 0.22rem;
  background-color: white;
  box-shadow: 0.005rem 0.005rem 0.005rem 0.005rem rgba(0, 0, 0, 0.08);
  -webkit-transform: translateX(-0.115rem);
  -webkit-transition: all 0.3s ease;
}
.invoiceBtn .switch:checked ~ label:after {
  -webkit-transform: translateX(0.115rem);
}
.invoiceBtn .switch:checked ~ label:before {
  background-color: #4bc6b1;
}



