  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  line-height: 1.6;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}
body,
html {
  background: #FFF;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
.price-original {
  color: #666;
