  text-decoration: line-through;
  margin-left: 0.05rem;
}
html,
body {
  background-color: #f7f7f7;
}
.container {
  padding-bottom: 0.6rem;
  position: inherit;
  overflow: hidden;
}
.intro .intro-divider {
  height: 0.25rem;
  text-align: center;
  display: inline;
}
.intro .intro-divider div {
  height: 100%;
  width: 40%;
  margin: 0 auto;
}
.intro .intro-divider div p {
  margin: 0.05rem;
  padding: 0.05rem;
  font-size: 0.09rem;
}
.intro .intro-divider div p img {
  width: 0.125rem;
  height: 0.125rem;
  margin-right: 0.05rem;
  vertical-align: bottom;
}
.intro .intro-vedio {
