
.slide-right-enter-active,
.slide-right-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-right-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  -webkit-transform: translateX(0.1rem);
          transform: translateX(0.1rem);
  opacity: 0;
}
.fade-in-enter-active,
.fade-in-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.fade-in-enter,
.fade-in-leave-to {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  opacity: 0;
}
html {
  font-family: sans-serif, Heiti SC, Helvetica, Droidsansfallback, Droid Sans, Dengxian, Arial;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  line-height: 1.6;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}
body,
html {
  background: #FFF;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
.price-original {
  color: #666;
  font-size: 0.1rem;
  font-style: normal;
  text-decoration: line-through;
  margin-left: 0.05rem;
}
.similar {
  background-color: #ffffff;
  padding: 0.1rem 0rem;
}
.similar .similar-demo {
  padding: 0 0.1rem;
  border-top: 0.005rem solid #e4e4e4;
  border-bottom: 0.005rem solid #e4e4e4;
  background-color: #ffffff;
}
.similar .similar-demo .list {
  min-height: 1.2rem;
  width: 100%;
  margin: 0.05rem 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding-top: 0.05rem;
}
.similar .similar-demo .list .list-left {
  float: left;
  display: inline-block;
  width: 1.15rem;
}
.similar .similar-demo .list .list-left img {
  width: 1.14rem;
  height: 1.14rem;
}
.similar .similar-demo .list .list-right {
  float: left;
  display: inline-block;
}
.similar .similar-demo .list .list-right .list-right-top {
  min-height: 0.6rem;
}
.similar .similar-demo .list .list-right .list-right-top p {
  padding-left: 0.25rem;
  color: #4a4a4a;
  font-size: 0.14rem;
  line-height: 0.175rem;
  margin-top: 0;
}
.similar .similar-demo .list .list-right .list-right-bottom .color {
  margin-bottom: 0.025rem;
  padding-left: 0.25rem;
  color: #4a4a4a;
  font-size: 0.11rem;
  font-weight: lighter;
}
.similar .similar-demo .list .list-right .list-right-bottom .price {
  padding-left: 0.25rem;
  margin: 0;
  color: #f93473;
  font-size: 0.15rem;
}
.similar .similar-con .similar-divider {
  height: 0.25rem;
  text-align: center;
  display: inline;
}
.similar .similar-con .similar-divider div {
  height: 100%;
  width: 50%;
  margin: 0 auto;
}
.similar .similar-con .similar-divider div p {
  margin: 0.05rem;
  padding: 0.05rem;
  font-size: 0.1rem;
  color: #9b9b9b;
}



