
.slide-right-enter-active,
.slide-right-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-right-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  -webkit-transform: translateX(0.1rem);
          transform: translateX(0.1rem);
  opacity: 0;
}
.fade-in-enter-active,
.fade-in-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.fade-in-enter,
.fade-in-leave-to {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  opacity: 0;
}
html {
  font-family: sans-serif, Heiti SC, Helvetica, Droidsansfallback, Droid Sans, Dengxian, Arial;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  line-height: 1.6;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}
body,
html {
  background: #FFF;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
.price-original {
  color: #666;
  font-size: 0.1rem;
  font-style: normal;
  text-decoration: line-through;
  margin-left: 0.05rem;
}
.advantage {
  margin: 0.1rem 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding: 0.1rem;
  background-color: #ffffff;
  border-top: 0.0025rem solid #e4e4e4;
  border-bottom: 0.0025rem solid #e4e4e4;
}
.advantage .pi-wrapper {
  width: 100%;
  color: #9b9b9b;
  padding-right: 0.4rem;
  background: url('../../assets/arrow-forward.png') no-repeat right;
  background-size: 0.1rem 0.175rem;
}
.advantage .pi-wrapper div {
  font-size: 0.12rem;
  display: inline-block;
