}
.adv-con .adv-con-list .adv-con-list-right {
  width: 65%;
  height: 100%;
  display: inline-block;
  float: left;
  padding-left: 5%;
}
.adv-con .adv-con-list .adv-con-list-right h2 {
  color: #4a4a4a;
