
.slide-right-enter-active,
.slide-right-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-right-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  -webkit-transform: translateX(0.1rem);
          transform: translateX(0.1rem);
  opacity: 0;
}
.fade-in-enter-active,
.fade-in-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.fade-in-enter,
.fade-in-leave-to {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  opacity: 0;
}
html {
  font-family: sans-serif, Heiti SC, Helvetica, Droidsansfallback, Droid Sans, Dengxian, Arial;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  line-height: 1.6;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}
body,
html {
  background: #FFF;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
.price-original {
  color: #666;
  font-size: 0.1rem;
  font-style: normal;
  text-decoration: line-through;
  margin-left: 0.05rem;
}
.pay {
  height: 0.355rem;
  line-height: 0.355rem;
}
.pay .payword {
  margin-left: 0.2rem;
  font-size: 0.15rem;
  color: #4a4a4a;
}
.pay .money {
  color: #f93473;
  font-size: 0.18rem;
  float: right;
  margin-right: 0.2rem;
}
.shadow {
  height: 0.1rem;
  background-color: #f3f3f3;
}
.weixin {
  display: -webkit-box;
}
.weixin .weixinimg {
  width: 0.53rem;
  height: 0.53rem;
  background: url('../../assets/wechat_normal@2x.png') no-repeat center center;
  background-size: contain;
  margin: 0.2rem;
}
.weixin .alipayimg {
  width: 0.53rem;
  height: 0.53rem;
  background: url('../../assets/Alipay_normal@2x.png') no-repeat center center;
  background-size: contain;
  margin: 0.2rem;
}
.weixin .weixinword {
  width: 1rem;
}
.weixin .weixinpay {
  margin-top: 0.225rem;
  color: #4a4a4a;
  font-size: 0.17rem;
  letter-spacing: 0.015rem;
}
.weixin .weixinsafe {
  color: #3f3a39;
  font-size: 0.12rem;
  letter-spacing: 0.015rem;
}
.weixin .person-icon {
  width: 0.2rem;
  height: 0.2rem;
  background: url('../../assets/Check_normal@2x.png') no-repeat center center;
  background-size: contain;
  position: absolute;
  right: 0.2rem;
  margin-top: 10%;
}
.weixin .person-icon.active {
  width: 0.21rem;
  height: 0.21rem;
  border-radius: 50%;
  display: block;
  border: 0.00025rem #9b9b9b;
  background: url('../../assets/Check_selected@2x.png') no-repeat center center;
  background-size: contain;
}
.underline {
  width: 100%;
  height: 0.005rem;
  border-bottom: 0.005rem solid #f3f3f3;
}
.sureToPay {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 0.44rem;
  width: 100%;
}
.sureToPay div {
  height: 100%;
  width: 100%;
  background-color: #4bc6b1;
  color: #ffffff;
  font-size: 0.17rem;
  text-align: center;
  line-height: 0.44rem;
}
.sureToPay div:active {
  opacity: 0.5;
}



